import styled from "styled-components";
import { bool, string } from "prop-types";
import React from "react";

import { setHeight } from "ui/mixins";
import { tdSymbol, negativeTdSymbol } from "assets/images";

const BrandSymbolEl = styled.img`
  ${({ size }) => setHeight(size)};
`;

const BrandSymbol = ({ size, negative }) => (
  <BrandSymbolEl src={negative ? negativeTdSymbol : tdSymbol} size={size} />
);

BrandSymbol.propTypes = {
  negative: bool,
  size: string
};

BrandSymbol.defaultProps = {
  size: "m",
  negative: null
};

export default BrandSymbol;
